import React,{useContext,useState,useEffect,Suspense, lazy} from 'react';
import { MyContext } from '../../contexts/MyContext';
import { WEB_URL_UPLOAD } from '../../contexts/Apilinks';
import {Link } from "react-router-dom"; 
import usePlacesAutocomplete from "use-places-autocomplete";
import { Combobox,ComboboxInput, ComboboxPopover,ComboboxList,ComboboxOption} from "@reach/combobox";
import "@reach/combobox/styles.css";
import { BiChevronDown,BiChevronUp } from "react-icons/bi";
let BaseURLuploads = WEB_URL_UPLOAD;

function SearchHome() {
   const {seachlistall} = useContext(MyContext);
   
   useEffect(() => {
      setValue('chennai')
      window.scrollTo(0, 0);
    }, []);
   const [islocation, setlocation] = useState({
      location:'chennai',
   });
  
   const {ready,value,suggestions: { status, data },setValue } = usePlacesAutocomplete();
   const handleInput = (e: React.ChangeEvent<HTMLInputElement>): void => {
     setValue(e.target.value);
     setlocation({
       ...islocation,
       location:e.target.value
     });
   };
   const handleSelect = (val: string): void => {
      setValue(val, false);
      setlocation({
        ...islocation,
        location:val
      });
      console.log(val)
    };
    const renderSuggestions = (): JSX.Element => {
      const suggestions = data.map(({ place_id, description }: any) => (
        <ComboboxOption key={place_id} value={description} />
      ));
    }
    
    const [istype, settype] = useState(false);
    const [istypename, settypename] = useState({
      typename:'Rent/Lease',
      listsss:'',
   });

   const typestatus = () =>{
      settype(istype === false ? true : false);
   }
   const clictyps = (types) =>{
      settype(false);
      settypename({
         ...istypename,
         typename:types,
         listsss:types,
       });

   }
   const clictypspg = (types) =>{
      settype(false);
      settypename({
         ...istypename,
         typename:types,
         listsss:types,
       });
       setpropertyname({
         ...ispropertyname,
         propertyname:'Occupancy Type',
       });
       setpropertypg(true);

   }
   const [ispropertypg, setpropertypg] = useState(false);
   const [isproperty, setproperty] = useState(false);
    const [ispropertyname, setpropertyname] = useState({
      propertyname:'House/Villa',
      pgboys:'',
      pggirls:'',
      privateroom:'',
      tworoom:'',
      twoplusroom:'',
   });

   const propertystatus = () =>{
      setproperty(isproperty === false ? true : false);
   }
   const clictypsproperty = (property) =>{
      setproperty(false);
      setpropertyname({
         ...ispropertyname,
         propertyname:property
       });
   }
   const onChangecheckroom = (ids,e) => {
      if (e.target.type === 'checkbox' && !e.target.checked){
         if(ids ==='Male'){ setpropertyname({...ispropertyname,pgboys:''});}
         if(ids ==='Female'){ setpropertyname({ ...ispropertyname,pggirls:''}); }
         if(ids ==='privateroom'){ setpropertyname({ ...ispropertyname,privateroom:''}); }
         if(ids ==='tworoom'){ setpropertyname({ ...ispropertyname,tworoom:''}); }
         if(ids ==='twoplusroom'){ setpropertyname({ ...ispropertyname,twoplusroom:''}); }

    } else{
      if(ids ==='Male'){  setpropertyname({...ispropertyname, pgboys:ids }); }
      if(ids ==='Female'){ setpropertyname({ ...ispropertyname, pggirls:ids }); }
      if(ids ==='privateroom'){ setpropertyname({ ...ispropertyname, privateroom:ids }); }
      if(ids ==='tworoom'){ setpropertyname({ ...ispropertyname, tworoom:ids }); }
      if(ids ==='twoplusroom'){ setpropertyname({ ...ispropertyname, twoplusroom:ids }); }
     
      
    }
  } 
   const [isBudget, setBudget] = useState(false);
    const [isBudgetname, setBudgetname] = useState({
      Budgetname:'Budget',
   });

   const Budgetstatus = () =>{
      setBudget(isBudget === false ? true : false);
   }
   const [ismin, setmin] = useState(true);
   const [ismax, setmax] = useState(false);
   const min = (e) => {
      setmin(true);
      setmax(false)
   }
   const max = (e) => {
      setmin(false);
      setmax(true);
   }
   const [BudgetInfo, setBudgetInfo] = useState({
      min:'',
      max:'',
   });
  
   const onChangeValuebudget = (e) => {
		setBudgetInfo({
			...BudgetInfo,
				[e.target.name]:e.target.value
		});
   }
   const clictypsBudgetmin = (Budget) =>{
      setBudgetInfo({
         ...BudgetInfo,
         min:Budget
       });
   }
   const clictypsBudgetmax = (Budget) =>{
      setmin(false);
      setBudget(false);
      setBudgetInfo({
         ...BudgetInfo,
         max:Budget
       });
   }
   // const clictypsBudget = (Budget) =>{
   //    setBudget(false);
   //    setBudgetname({
   //       ...isBudgetname,
   //       Budgetname:Budget
   //     });
   // }
   const [issearchlist, setsearchlist] = useState([]);
   const seachlist = async() =>{
      try {
         const seachlistsss = await seachlistall(islocation,istypename,ispropertyname,BudgetInfo,isBudgetname);
         if(seachlistsss.success === 1){
           console.log('seachlistsss',seachlistsss.seachviews);
            if(seachlistsss.success === 1){
               setsearchlist(seachlistsss.seachviews);
            
            }
         }
       } catch (error) { throw error;}
      }

     

  return (
   <>
   
   <div className="clearfix"></div>
<div className="loc__searsss"> 
   <div className="container"> 
      <div className="row"> 
      <div className="col-md-12"> 
         <div className="loc__searsss__two"> 
            <div className="col-md-2"> 
               <div className="loc__searsss__types"> 
                  <p onClick={typestatus}> {istypename.typename === 'Rent/Lease' ? <> Rent  </> : <> {istypename.typename} </> }  {istype === true ? <BiChevronUp /> : <BiChevronDown /> }</p>
                  {istype === true ? 
                     <div className="loc__searsss__types__two"> 
                       <h4> RESIDENTIAL </h4>
                       <ul>
                          <li onClick={()=>clictyps('Buy')}> Buy </li>
                          <li onClick={()=>clictyps('Rent/Lease')}> Rent </li>
                          <li onClick={()=>clictypspg('PG')}> PG </li>
                       </ul>
                       
                       <div className="clearfix"></div>
                      <h4> COMMERCIAL </h4>
                       <ul>
                          <li onClick={()=>clictyps('Buy')}> Buy </li>
                          <li onClick={()=>clictyps('Rent/Lease')}> Rent </li>
                       </ul>
                     
                     </div>
                  : null}
               </div>
            </div>
            <div className="col-md-5"> 
               <Combobox onSelect={handleSelect} aria-labelledby="demo" className="form-control-mapsqqq">
                  <ComboboxInput
                  style={{ width: "100%", maxWidth: "100%" }}
                  value={value}
                  onChange={handleInput}
                  disabled={!ready}
                  required
                  />
                  <ComboboxPopover>
                     <ComboboxList>{status === "OK" && renderSuggestions()}</ComboboxList>
                  </ComboboxPopover>
               </Combobox>
            </div> 
            <div className="col-md-2"> 
               <div className="loc__searsss__types" > 
               {ispropertypg === true ? <>  
                  <p onClick={propertystatus}> {ispropertyname.propertyname} {isproperty === true ? <BiChevronUp /> : <BiChevronDown /> }</p>
                  {isproperty === true ? 
                     <div className="loc__searsss__types__two"> 
                       <h4> Gender </h4>
                       <ul> 
                          <li>
                             <input type="checkbox" id="Boys" name="pgboys" value="Male" onChange={(e)=>onChangecheckroom('Male',e)} checked={ispropertyname.pgboys === 'Male'} />
                             <label htmlFor="Boys"> Boys  </label>
                           </li>
                           <li>
                             <input type="checkbox" id="Female" name="pggirls" value="Female" onChange={(e)=>onChangecheckroom('Female',e)} checked={ispropertyname.pggirls === 'Female'}/>
                             <label htmlFor="Female"> Girls  </label>
                           </li>
                        </ul>
                      <div className="clearfix"></div>
                       <h4>Sharing</h4>
                       <ul>
                      
                        <li>
                           <input type="checkbox" id="privateroom" name="privateroom" value="privateroom" onChange={(e)=>onChangecheckroom('privateroom',e)} checked={ispropertyname.privateroom === 'privateroom'}/>
                             <label htmlFor="privateroom"> Private Room  </label>
                        </li>
                        <li>
                           <input type="checkbox" id="tworoom" name="tworoom" value="Private" onChange={(e)=>onChangecheckroom('tworoom',e)} checked={ispropertyname.tworoom === 'tworoom'}/>
                             <label htmlFor="tworoom"> 2 per Room </label>
                        </li>
                        <li>
                           <input type="checkbox" id="twoplusroom" name="twoplusroom" value="twoplusroom" onChange={(e)=>onChangecheckroom('twoplusroom',e)} checked={ispropertyname.twoplusroom === 'twoplusroom'}/>
                             <label htmlFor="twoplusroom"> 2+ per Room </label>
                        </li>
                       </ul>
                       
                       </div>
                       : null }

                  
                   </> : <>   
                  <p onClick={propertystatus}> {ispropertyname.propertyname} {isproperty === true ? <BiChevronUp /> : <BiChevronDown /> }</p>
                  {isproperty === true ? 
                     <div className="loc__searsss__types__two"> 
                       <h4> RESIDENTIAL </h4>
                       <ul>
                          <li onClick={()=>clictypsproperty('Flat')}> Flat </li>
                          <li onClick={()=>clictypsproperty('House/Villa')}>  House/Villa </li>
                          
                        {istypename.listsss === 'Buy' ? <>  
                          <li onClick={()=>clictypsproperty('Plot')}>  Plot </li>
                          </>  : null }
                       </ul>
                       <div className="clearfix"></div>
                      <h4> COMMERCIAL </h4>
                       <ul>
                          <li onClick={()=>clictypsproperty('Office Space')}> Office Space </li>
                          <li onClick={()=>clictypsproperty('Shop/Showroom')}> Shop/Showroom </li>
                          <li onClick={()=>clictypsproperty('Commercial Land')}> Commercial Land </li>
                          {/* <li onClick={()=>clictypsproperty('Co-working Space')}> Co-working Space </li> */}
                          <li onClick={()=>clictypsproperty('Warehouse/Godow')}> Warehouse/ Godown </li>
                          <li onClick={()=>clictypsproperty('Industrial Building')}> Industrial Building </li>
                          <li onClick={()=>clictypsproperty('Industrial Shed')}>  Industrial Shed </li>
                       </ul>
                     <div className="clearfix"></div>
                      <h4> OTHERS </h4>
                       <ul>
                          <li onClick={()=>clictyps('Agricultural Land')}>  Agricultural Land </li>
                          <li onClick={()=>clictyps('Farm House')}>  Farm House </li>
                       </ul>
                     
                     </div>
                  : null}
                  </> }
               </div>
            </div>
             <div className="col-md-2"> 
               <div className="loc__searsss__types"> 
           

         <p onClick={Budgetstatus}> 
            {BudgetInfo.min === '' && BudgetInfo.max === '' ? <>   
                  Budget
            </> : null }
            {BudgetInfo.min !== '' && BudgetInfo.max !== '' ? <>   
                  {BudgetInfo.min} - {BudgetInfo.max}
            </> : null }
            {BudgetInfo.min !== '' && BudgetInfo.max === '' ? <>   
                  Greater than {BudgetInfo.min}
            </> : null }
            {BudgetInfo.min === '' && BudgetInfo.max !== '' ? <>   
                  Upto {BudgetInfo.max}
            </> : null } 
            {isBudget === true ? <BiChevronUp /> : <BiChevronDown /> 
            }
         </p>
                  {isBudget === true ? 
                     <div className="loc__searsss__types__two"> 

                     	
                     	
               <div className="col-md-6">
               <input type="text" name="min" value={BudgetInfo.min} onChange={onChangeValuebudget} onClick={min} className="form-control" placeholder="Min Price"    />
                  {ismin === true ? <>
                     <p> Min </p>
                       <ul>
                          {istypename.listsss === 'Buy' ? <>  
                          <li onClick={()=>clictypsBudgetmin('5 Lac')}> ₹5 Lac </li>
                          <li onClick={()=>clictypsBudgetmin('10 Lac')}> ₹10 Lac </li>
                          <li onClick={()=>clictypsBudgetmin('20 Lac')}> ₹20 Lac </li>
                          <li onClick={()=>clictypsBudgetmin('30 Lac')}> ₹30 Lac </li>
                          <li onClick={()=>clictypsBudgetmin('40 Lac')}> ₹40 Lac </li>
                          <li onClick={()=>clictypsBudgetmin('50 Lac')}> ₹50 Lac0 </li>
                          <li onClick={()=>clictypsBudgetmin('60 Lac')}> ₹60 Lac </li>
                          <li onClick={()=>clictypsBudgetmin('70 Lac')}> ₹70 Lac</li>
                          <li onClick={()=>clictypsBudgetmin('80 Lac')}> ₹80 Lac </li>
                          <li onClick={()=>clictypsBudgetmin('90 Lac')}> ₹90 Lac </li>
                          <li onClick={()=>clictypsBudgetmin('1 Cr')}> ₹1 Cr </li>
                          <li onClick={()=>clictypsBudgetmin('1.5 Cr')}> ₹1.5 Cr </li>
                          <li onClick={()=>clictypsBudgetmin('2 Cr')}> ₹20 Cr </li>
                          <li onClick={()=>clictypsBudgetmin('3 Cr')}> ₹3 Cr </li>
                          <li onClick={()=>clictypsBudgetmin('7 Cr')}> ₹7 Cr </li>
                          <li onClick={()=>clictypsBudgetmin('10 Cr')}> ₹10 Cr </li>
                          <li onClick={()=>clictypsBudgetmin('20 Cr')}> ₹20 Cr </li>
                          <li onClick={()=>clictypsBudgetmin('30 Cr')}> ₹30 Cr </li>
                          <li onClick={()=>clictypsBudgetmin('40 Cr')}> ₹40 Cr </li>
                          <li onClick={()=>clictypsBudgetmin('50 Cr')}> ₹50 Cr </li>
                          <li onClick={()=>clictypsBudgetmin('60 Cr')}> ₹60 Cr </li>
                          <li onClick={()=>clictypsBudgetmin('70 Cr')}> ₹70 Cr </li>
                          <li onClick={()=>clictypsBudgetmin('80 Cr')}> ₹80 Cr </li>
                          <li onClick={()=>clictypsBudgetmin('90 Cr')}> ₹90 Cr </li>
                          <li onClick={()=>clictypsBudgetmin('100 Cr')}> ₹100 Cr </li>=
                          
                           </> : <> 
                          <li onClick={()=>clictypsBudgetmin('5000')}> ₹5000 </li>
                          <li onClick={()=>clictypsBudgetmin('10000')}> ₹10000 </li>
                          <li onClick={()=>clictypsBudgetmin('15000')}> ₹15000 </li>
                          <li onClick={()=>clictypsBudgetmin('20000')}> ₹20000 </li>
                          <li onClick={()=>clictypsBudgetmin('25000')}> ₹25000 </li>
                          <li onClick={()=>clictypsBudgetmin('30000')}> ₹30000 </li>
                          <li onClick={()=>clictypsBudgetmin('35000')}> ₹35000 </li>
                          <li onClick={()=>clictypsBudgetmin('40000')}> ₹40000 </li>
                          <li onClick={()=>clictypsBudgetmin('50000')}> ₹50000 </li>
                          <li onClick={()=>clictypsBudgetmin('60000')}> ₹60000 </li>
                          <li onClick={()=>clictypsBudgetmin('85000')}> ₹85000 </li>
                          <li onClick={()=>clictypsBudgetmin('1 Lac')}> ₹1 Lac </li>
                          <li onClick={()=>clictypsBudgetmin('1.5 Lac')}> ₹1.5 Lac </li>
                          <li onClick={()=>clictypsBudgetmin('1.5 Lac')}> ₹1.5 Lac </li>
                          <li onClick={()=>clictypsBudgetmin('2 Lac')}> ₹2 Lac </li>
                          <li onClick={()=>clictypsBudgetmin('7 Lac')}> ₹7 Lac </li>
                          <li onClick={()=>clictypsBudgetmin('10 Lac')}> ₹10 Lac </li>
                        </>}
                       </ul>
                    
                  </>: <> &nbsp; </>}
                   </div>
                  <div className="col-md-6">
                   <input type="text" name="max" value={BudgetInfo.max} onChange={onChangeValuebudget} onClick={max} className="form-control" placeholder="Max Price"    />
                  {ismax === true ? <>
                     
                        <p>Max</p>

                       <ul>
                       {istypename.listsss === 'Buy' ? <>  
                          <li onClick={()=>clictypsBudgetmin('5 Lac')}> ₹5 Lac </li>
                          <li onClick={()=>clictypsBudgetmin('10 Lac')}> ₹10 Lac </li>
                          <li onClick={()=>clictypsBudgetmin('20 Lac')}> ₹20 Lac </li>
                          <li onClick={()=>clictypsBudgetmin('30 Lac')}> ₹30 Lac </li>
                          <li onClick={()=>clictypsBudgetmin('40 Lac')}> ₹40 Lac </li>
                          <li onClick={()=>clictypsBudgetmin('50 Lac')}> ₹50 Lac0 </li>
                          <li onClick={()=>clictypsBudgetmin('60 Lac')}> ₹60 Lac </li>
                          <li onClick={()=>clictypsBudgetmin('70 Lac')}> ₹70 Lac</li>
                          <li onClick={()=>clictypsBudgetmin('80 Lac')}> ₹80 Lac </li>
                          <li onClick={()=>clictypsBudgetmin('90 Lac')}> ₹90 Lac </li>
                          <li onClick={()=>clictypsBudgetmin('1 Cr')}> ₹1 Cr </li>
                          <li onClick={()=>clictypsBudgetmin('1.5 Cr')}> ₹1.5 Cr </li>
                          <li onClick={()=>clictypsBudgetmin('2 Cr')}> ₹20 Cr </li>
                          <li onClick={()=>clictypsBudgetmin('3 Cr')}> ₹3 Cr </li>
                          <li onClick={()=>clictypsBudgetmin('7 Cr')}> ₹7 Cr </li>
                          <li onClick={()=>clictypsBudgetmin('10 Cr')}> ₹10 Cr </li>
                          <li onClick={()=>clictypsBudgetmin('20 Cr')}> ₹20 Cr </li>
                          <li onClick={()=>clictypsBudgetmin('30 Cr')}> ₹30 Cr </li>
                          <li onClick={()=>clictypsBudgetmin('40 Cr')}> ₹40 Cr </li>
                          <li onClick={()=>clictypsBudgetmin('50 Cr')}> ₹50 Cr </li>
                          <li onClick={()=>clictypsBudgetmin('60 Cr')}> ₹60 Cr </li>
                          <li onClick={()=>clictypsBudgetmin('70 Cr')}> ₹70 Cr </li>
                          <li onClick={()=>clictypsBudgetmin('80 Cr')}> ₹80 Cr </li>
                          <li onClick={()=>clictypsBudgetmin('90 Cr')}> ₹90 Cr </li>
                          <li onClick={()=>clictypsBudgetmin('100 Cr')}> ₹100 Cr </li>=
                          
                           </> : <> 
                          <li onClick={()=>clictypsBudgetmax('5000')}> ₹5000 </li>
                          <li onClick={()=>clictypsBudgetmax('10000')}> ₹10000 </li>
                          <li onClick={()=>clictypsBudgetmax('15000')}> ₹15000 </li>
                          <li onClick={()=>clictypsBudgetmax('20000')}> ₹20000 </li>
                          <li onClick={()=>clictypsBudgetmax('25000')}> ₹25000 </li>
                          <li onClick={()=>clictypsBudgetmax('30000')}> ₹30000 </li>
                          <li onClick={()=>clictypsBudgetmax('35000')}> ₹35000 </li>
                          <li onClick={()=>clictypsBudgetmax('40000')}> ₹40000 </li>
                          <li onClick={()=>clictypsBudgetmax('50000')}> ₹50000 </li>
                          <li onClick={()=>clictypsBudgetmax('60000')}> ₹60000 </li>
                          <li onClick={()=>clictypsBudgetmax('85000')}> ₹85000 </li>
                          <li onClick={()=>clictypsBudgetmax('1 Lac')}> ₹1 Lac </li>
                          <li onClick={()=>clictypsBudgetmax('1.5 Lac')}> ₹1.5 Lac </li>
                          <li onClick={()=>clictypsBudgetmax('1.5 Lac')}> ₹1.5 Lac </li>
                          <li onClick={()=>clictypsBudgetmax('2 Lac')}> ₹2 Lac </li>
                          <li onClick={()=>clictypsBudgetmax('7 Lac')}> ₹7 Lac </li>
                          <li onClick={()=>clictypsBudgetmax('10 Lac')}> ₹10 Lac </li>
                        </>}
                       </ul>
                     </>: <> &nbsp; </>}
                     </div>
                  
                       <div className="clearfix"></div>
                      
                    
                     </div>
                  : null}
               </div>
            </div>
            <div className="col-md-3"> 
            <button className="loc__searsss__btn" onClick={seachlist}> Search </button>
            </div> 
         </div> 
      </div> 
      </div> 
   </div> 
</div> 
   </>
  );
}

export default SearchHome;